.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #fff;
  background-color: #000;
}

.App-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0rem;
}

@media (max-width: 600px) {
  .App-header {
  flex-direction: column;
  }
  }
  @media (max-width: 600px) {
    .App-nav ul {
    flex-direction: column;
    }
    }
    @media (max-width: 600px) {
      .App-logo {
      width: 4rem;
      }
      }
.App-logo-link {
  display: flex;
  align-items: center;
  color: #fff;
  text-decoration: none;
  height: 4rem;
  margin-left: -6rem;
}

.App-logo {
  width: 25rem; /* or 6rem */
  margin-right: 0.5rem;
}


.App-search-form {
  display: flex;
  align-items: center;
}

.App-search-form input {
  flex: 1;
  padding: 0.5rem;
  border: none;
  background-color: #222;
  color: #fff;
}

.App-search-form button {
  padding: 0.5rem;
  border: none;
  background-color: #e50914;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
}

.App-nav {
  display: flex;
}

@media screen and (max-width: 600px) {
  .App-nav {
    display: none; /* hide the menu on small screens */
  }
}

@media (max-width: 768px) {
  .App-nav-container {
    display: none;
  }
}


.App-nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.App-nav li {
  margin: 0 0.5rem;
}

.App-nav a {
  display: block;
  padding: 0.5rem;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

.App-nav a:hover {
  background-color: #e50914;
}

.App-main {
  flex: 1;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 600px) {
  .App-main {
    flex-direction: column;
  }
}


.App-carousel {
  display: flex;
  overflow-x: scroll;
}

.App-carousel::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.App-carousel-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 1rem;
}

.App-carousel-item img {
  width: 15rem;
  height: 22.5rem;
  object-fit: cover;
  border-radius: 0.5rem;
}

.App-carousel-item h3 {
  margin: 0.5rem 0;
  font-size: 1.25rem;
  font-weight: bold;
  text-align: center;
}

.App-footer {
  padding: 1rem;
  background-color: #222;
}

.App-footer-links {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.App-footer-links a {
  color: #fff;
  text-decoration: none;
  font-size: 0.875rem;
}

.App-footer-copyright {
  color: #999;
  font-size: 0.75rem;
}
